.story {
    margin: 0 auto;
    max-width: 700px;

    &__heading {
        font-size: 60px;
        margin: 0 0 60px;

    }

    &__subheading {
        font-size: 40px !important;
        margin: 60px 0 40px !important;
    }

    &__text {
        font-family: "Montserrat", sans-serif;
        letter-spacing: 1.25px;
        font-size: 16px;
        line-height: 25px;
        margin: 20px auto;
        text-align: left;

        @media (min-width: 600px) {
            max-width: 700px;
        }
    }

    &__photo {
        width: 50%;
        margin: 10px auto;
    }
}

.timeline {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-content: center;
    margin-bottom: 50px;

    img {
        width: 100%;
        max-width: 300px;
    }

    p {
        box-sizing: border-box;
        width: 100%;
        max-width: 350px;
        border-top: double 6px #b8d9a5;
        border-bottom: double 6px #b8d9a5;
        border-radius: 10%;
        padding: 30px;
        font-size: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: "Montserrat", sans-serif;
    }
}